import { Component, Directive, EventEmitter, HostBinding, Input, Output, TemplateRef, ElementRef, OnInit } from '@angular/core';
import { TabsetComponent } from './tabsetComponent';


export class TabDirective implements OnInit {
  /** tab header text */
   public heading: string;
  /** if true tab can not be activated */
   public disabled: boolean;
  /** if true tab can be removable, additional button will appear */
   public removable: boolean;
  /** if set, will be added to the tab's class atribute */
   public customClass: string;

  /** tab active state toggle */
  
  
  public get active(): boolean {
    return this._active;
  }

  public set active(active: boolean) {
   
    if (this.disabled && active || !active) {
      if (!active) {
        this.removeClass(this.el.nativeElement, 'show');
        setTimeout(() => {
          this._active = active;
          this.deselect.emit(this);
        }, 150);      
      }

      
      return;
    }

   
    setTimeout(() => {
      this._active = active;
      this.classAdd(this.el.nativeElement, 'show'); 
    }, 150);  
    this.select.emit(this);
    
    this.tabset.tabs.forEach((tab: TabDirective) => {
      if (tab !== this) {
        tab.active = false;
      } 
    });
    
  }

  /** fired when tab became active, $event:Tab equals to selected instance of Tab component */
   public select: EventEmitter<TabDirective> = new EventEmitter();
  /** fired when tab became inactive, $event:Tab equals to deselected instance of Tab component */
   public deselect: EventEmitter<TabDirective> = new EventEmitter();
  /** fired before tab will be removed */
   public removed: EventEmitter<TabDirective> = new EventEmitter();

   public addClass: boolean = true;
   public test: boolean = true;


  public headingRef: TemplateRef<any>;
  public tabset: TabsetComponent;
  public el: ElementRef = null;

  protected _active: boolean;

  public constructor(tabset: TabsetComponent, el: ElementRef) {
    this.el = el;
    this.tabset = tabset;
    this.tabset.addTab(this);
  }

  public ngOnInit(): void {
    this.removable = this.removable;
  }


  protected hasClass(el: any, className: any) {
  if (el.classList)
    return el.classList.contains(className);
  else
    return !!el.className.match(new RegExp('(\\s|^)' + className + '(\\s|$)'));
}

protected classAdd(el: any, className: any) {
  if (el.classList)
    el.classList.add(className);
  else if (!this.hasClass(el, className)) el.className += " " + className;
}

protected removeClass(el: any, className: any) {
  if (el.classList)
    el.classList.remove(className);
  else if (this.hasClass(el, className)) {
    var reg = new RegExp('(\\s|^)' + className + '(\\s|$)');
    el.className=el.className.replace(reg, ' ');
  }
}
static decorators: DecoratorInvocation[] = [
{ type: Directive, args: [{selector: 'tab, [tab]'}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: TabsetComponent, },
{type: ElementRef, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'heading': [{ type: Input },],
'disabled': [{ type: Input },],
'removable': [{ type: Input },],
'customClass': [{ type: Input },],
'active': [{ type: HostBinding, args: ['class.active', ] },{ type: Input },],
'select': [{ type: Output },],
'deselect': [{ type: Output },],
'removed': [{ type: Output },],
'addClass': [{ type: HostBinding, args: ['class.tab-pane', ] },],
'test': [{ type: HostBinding, args: ['class.fade', ] },],
};
}
interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
